import React, { useState, useEffect } from "react";
import img from './images/todo.svg';
import "./style.css";
import { Helmet } from 'react-helmet-async';
import Favicon from 'react-favicon'

// get the localStorage data back
const getLocalData = () => {
  const lists = localStorage.getItem("mytodolist");

  if (lists) {
    return JSON.parse(lists);
  } else {
    return [];
  }
};

const Todo = (prop) => {
  const [inputdata, setInputData] = useState("");
  const [items, setItems] = useState(getLocalData());
  const [isEditItem, setIsEditItem] = useState("");
  const [toggleButton, setToggleButton] = useState(false);

  // add the items fucnction
  const addItem = () => {
    if (!inputdata) {
      alert("plz fill the data");
    } else if (inputdata && toggleButton) {
      setItems(
        items.map((curElem) => {
          if (curElem.id === isEditItem) {
            return { ...curElem, name: inputdata };
          }
          return curElem;
        })
      );

      setInputData("");
      setIsEditItem(null);
      setToggleButton(false);
    } else {
      const myNewInputData = {
        id: new Date().getTime().toString(),
        name: inputdata,
      };
      setItems([...items, myNewInputData]);
      setInputData("");
    }
  };

  //edit the items
  const editItem = (index) => {
    const item_todo_edited = items.find((curElem) => {
      return curElem.id === index;
    });
    setInputData(item_todo_edited.name);
    setIsEditItem(index);
    setToggleButton(true);
  };

  // how to delete items section
  const deleteItem = (index) => {
    const updatedItems = items.filter((curElem) => {
      return curElem.id !== index;
    });
    setItems(updatedItems);
  };

  // remove all the elements
  const removeAll = () => {
    setItems([]);
  };

  // adding localStorage
  useEffect(() => {
    localStorage.setItem("mytodolist", JSON.stringify(items));
  }, [items]);

  return (
    <>
      <div className="main-div_todo">
        <Favicon url={img} />
        <Helmet>
          <title>ToDoList</title>
          <meta name='description' content='Beginner friendly page for learning React Helmet.' />
        </Helmet>
        <div className="child-div card p-3 mb-4 shadow rounded border" style={(prop.mode === 'dark') ? { backgroundColor: 'gray', color: 'black' } : { backgroundColor: '#7676ff', color: 'white' }}>
          <figure>
            <img src={img} alt="todologo" />
            <figcaption>Add Your List Here ✌</figcaption>
          </figure>
          <div className="addItems">
            <input
              type="text"
              placeholder="   ✍ Add Item"
              className="form-control"
              value={inputdata}
              style={{ fontSize: '1rem' }}
              onChange={(event) => setInputData(event.target.value)}
            />
            {toggleButton ? (
              <div className="addTodo">
                <i className="far fa-edit add-btn" onClick={addItem}></i>
              </div>
            ) : (
              <div className="addTodo">
                <i className="fa fa-plus add-btn" onClick={addItem}></i>
              </div>
            )}
          </div>
          {/* show our items  */}
          <div className="showItems">
            {items.map((curElem) => {
              return (
                <div className="eachItem border" key={curElem.id} style={(prop.mode === 'dark') ? { backgroundColor: 'gray', color: 'black' } : { backgroundColor: '#7676ff', color: 'white' }}>
                  <h5>{curElem.name}</h5>
                  <div className="todo-btn">
                    <i
                      className="far fa-edit add-btn"
                      onClick={() => editItem(curElem.id)}></i>
                    <i
                      className="far fa-trash-alt add-btn"
                      onClick={() => deleteItem(curElem.id)}></i>
                  </div>
                </div>
              );
            })}
          </div>

          {/* rmeove all button  */}
          <div className="showItems">
            <button
              className="btn todobtn effect04"
              data-sm-link-text="Remove All"
              onClick={removeAll}>
              <span> CHECK LIST</span>
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default Todo;