import React from 'react'

export const Alert = (prop) => {
  return (
    <div style={prop.alert.type === 'success' ? { display: "block" } : { display: "hidden", height: "5rem" }}>
      <div className={`alert alert-${prop.alert.type}`} role="alert">
        {prop.alert.msg}
      </div>
    </div>
  )
}
